import React, { useEffect, useState } from "react";
import {useLocation, useNavigate} from 'react-router-dom';
import { useParams } from "react-router-dom";

//dependencias
import * as VideoServer from "../../services/videoServer";
import * as SerieServer from "../../services/serie";
import VideoLoadingComponent from "./videoLoading";


import * as HistorialUserServer from "../../services/historialUser";
import * as commentaryServer from "../../services/commentary";
import { useModal } from "../../hooks/useModal";

//MaterialUI
import Box from "@mui/material/Box";
import Button from '@mui/material/Button';
import Container from "@material-ui/core/Container";
import StarIcon from "@mui/icons-material/Star";
import { makeStyles } from "@material-ui/core/styles";
import StarBorderIcon from "@mui/icons-material/StarBorder";
import Stack from '@mui/material/Stack';
import Typography from "@material-ui/core/Typography";

//components
import "../../styles/styles.css";
import ModalComentario from "./comentarioModal";
import ModalComentarios from "./listComentariosModal";
import IframeVideo from "./iframeVideo";

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(0),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
}));
/**
 * Función para mostrar el detalle de un video y guardar la puntuación.
 * @returns Componente que muestra el detalle de un video junto con el iframe
 */
const VideoDetail = () => {
  
  const history = useNavigate();
  const [show, handleShow, handleClose] = useModal(false);
  const [show2, handleShow2, handleClose2] = useModal(false);
  
  const location = useLocation();
  const { id } = useParams();
  const [video, setVideo] = useState([]);
  const [serie, setSerie] = useState([]);
  const [loading, setLoading] = useState(true);
  const [duracion, setDuracion] = useState([]);
  const [uploadDate, setUploadDate] = useState(null);
  const [histUser, setHistUser] = useState(location.state);
  const [commentaries, setCommentaries] = useState([]);
  const [activeStar, setActiveStar] = useState(-1);
  const totalStars = 5;
  const [appState, setAppState] = useState({
    loading: true
  });
  const VideoLoading = VideoLoadingComponent(video);
  
  /**
   * Función para almacenar la duración del video en un formato adecuado para mostrarla en pantalla.
   * @param {time} duration 
   * @returns Duración en el formato adecuado.
   */
  const changeDuration = (duration) => {
    let tiempo = duration.split(':');
    let new_duration = "";
    if (tiempo) {
      if (tiempo[0] !== "00") {
        new_duration = tiempo[0] + "h";
      }
      if (tiempo[1] !== "00") {
        new_duration = " " + new_duration + " " + tiempo[1] + "min";
      }
      /* if (tiempo[2] !=="00") {
        new_duration = " "+new_duration+" "+tiempo[2]+"s";
      } */
    }
    return new_duration.trim(); // trim() para eliminar cualquier espacio en blanco adicional
  };
  

  /**
   * Función para obtener la información del video en reproducción
   */
  useEffect(() => {
    const getVideo = async (videoID) => {
      const res = await VideoServer.getVideo(videoID);
      const video = res;
      console.log(video);

      setUploadDate(new Date(res.upload_date).toDateString());
      setDuracion(changeDuration(res.duration));
      setVideo({
        ...video,
        duration: duracion,           
      });
      setAppState({ loading: false});
      
    };
    getVideo(id);

  }, [duracion, id, setVideo, video.title_espanol, video.url_esp, video.url_vimeo_esp,video.serie_id]);
  
  
  useEffect(() => {
    const getSeries = async (serieID) => {
      const res = await SerieServer.getSerie();
      const serie = res;
      console.log(serie);

      setSerie({
        ...serie,
      });
    };
    getSeries(video.serie);

  }, []);
  

   useEffect(() => {      
    if (histUser) {
      setActiveStar(histUser.user_score-1);
    } else {
      history("/loginuser");      
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []) 
  
  useEffect(() => {
    const getCommentaries = async (videoId) => {
      const res = await commentaryServer.ListCommentaryVideo({'video_id':videoId});
      setCommentaries(res);
    }
    getCommentaries(id);
  }, [id]);
  /**
   * Función para cambiar la calificación del video.
   * @param {int} index 
   */
  const handleClick = async (index) => {
    setActiveStar(index);
    let cumulative_score = video.cumulative_score;
    let number_votes = video.numberOfVotes;

    if (histUser.user_score) {
      cumulative_score = cumulative_score - histUser.user_score + index+1;
      const score_video = cumulative_score/number_votes;
      setHistUser({...histUser, user_score:index+1});
      await VideoServer.partialUpdateVideo(video.id, {'cumulative_score': cumulative_score, 'score': score_video})

    } else {
      number_votes +=1;
      cumulative_score = cumulative_score + index+1;
      const score_video = cumulative_score/number_votes;
      setHistUser({...histUser, user_score:index+1});
      await VideoServer.partialUpdateVideo(video.id, {'cumulative_score': cumulative_score,'numberOfVotes': number_votes,'score': score_video})
    }
    await HistorialUserServer.updateHistorialUser(histUser.id,{'user_score': index+1});
 
  };
  
  const classes = useStyles();

  if (histUser) {
    return (
    <Container>
      {appState.loading  ?  <VideoLoading isLoading={appState.loading} video={video} /> :""}
       
      <div>
        <div className="d-flex justify-content-end">
          <button  onClick={() => window.history.back()} className="btn btn-light" value="Volver">Volver</button>
        </div>
      </div>
      <div className={classes.paper}>
        <h3>

          {video.title_espanol}
        </h3>
      </div>
      <br></br>
      <div className="row">
        <div className="col-md-7 col-12 iframe1">
          <IframeVideo
            video={video}
            >
          </IframeVideo>
        </div>
        <div className="col-md-5 col-12 infoVideo" >
          <Typography component="h5" variant="h6" style={{color:"white",borderBottom:"3px solid white",paddingBottom:'5px'}} align="center">
            {video.duration}
          </Typography>
          <br></br>
          
          <br></br>
          <Typography component="h5" variant="h6">
            Ayudanos a calificar   &nbsp;
            <Box 
              sx={{
                display: "inline-flex",
                position: "relative",
                cursor: "pointer",
                textAlign: "left",
              }}
            >
              {[...new Array(totalStars)].map((arr, index) => {
                return (
                  <Box 
                  key={index} 
                  position="relative"
                  sx={{
                    cursor: "pointer",
                  }}
                  onClick={(e) => handleClick(index)}
                  >
                    <Box 
                      sx={{
                        width: index <= activeStar ? "100%" : "0%",
                        overflow: "hidden",
                        position: "absolute",
                      }}
                    >
                      <StarIcon />
                    </Box>
                    <Box>
                      <StarBorderIcon />
                    </Box>
                  </Box>
                )
              })}
            </Box>
          </Typography>
          <br></br>
          <div className="d-flex ">

          <Stack  alignItems="center" className="p-2">
            <Button 
            variant="contained" 
            color="success"
            type="submit"
            onClick={handleShow}>
              Realizar comentario
            </Button>
          </Stack>
          {commentaries ? 
            <Stack  alignItems="center" className="p-2">
              <Button 
                variant="contained" 
                color="primary"
                type="submit"
                onClick={handleShow2}>
                Ver otros comentarios
              </Button>
            </Stack>
          : 
          null
        }
        </div>
        </div>
        <p className="mt-3">
            {video.description_esp}
        </p>
        <br/>
        <div className="d-flex justify-content-between">
          {video.categorias && (
            <ul>
              Categorias:
              {video.categorias.map(categoria => (
                <li key={categoria.id}><small className="">{categoria.categoria}</small></li>
              ))}
            </ul>
          )}
          {video.especialidad && (
            <ul>
              Especialidades:
              {video.especialidad.map(especialidad => (
                <li key={especialidad.id}><small className="">{especialidad.especialidad}</small></li>
              ))}
            </ul>
          )}
          </div>
          <div className="d-flex justify-content-between border-top  pt-2 border-2 ">

          {video.subEspecialidad && (
            <ul>
              Subespecialidades:
              {video.subEspecialidad.map(subEspecialidad => (
                <li key={subEspecialidad.id}><small className="">{subEspecialidad.subEspecialidad}</small></li>
              ))}
            </ul>
          )}
          {video.palabraClave && (
            <ul>
              Palabras claves:
              
              {video.palabraClave.map(palabraClave => (
                <li key={palabraClave.id}><small className="">{palabraClave.palabraClave}</small></li>
              ))}
            </ul>
          )}
          </div>
      </div>
      <ModalComentario
        handleClose={handleClose}
        show={show}
        histUser={histUser}
        videoID ={id}
      ></ModalComentario>
      <ModalComentarios
        handleClose={handleClose2}
        show={show2}
        histUser={histUser}
        commentaries = {commentaries}
        videoID= {id}
      ></ModalComentarios>
    </Container>
    );
  }
  return (
    <p style={{ fontSize: "25px" }}>Inicia sesión para ver todos los videos!</p>
  );
  
};

export default VideoDetail;
